import {Select, Option, Tag} from 'element-ui';

export default {
    components: {
        [Button.name]: Button,
        [Checkbox.name]: Checkbox,
        [Radio.name]: Radio,
        [FormGroupInput.name]: FormGroupInput,
        [Switch.name]: Switch,
        [Select.name]: Select,
        [Option.name]: Option,
        [DropDown.name]: DropDown,
        [Tag.name]: Tag,
        Slider
    },
    data() {
        return {
            singleSelect: {
